html {
  display: block;
  position: relative;
  overflow-x: hidden;
  width: 100vw;
}
html::-webkit-scrollbar {
  width: 0;
}

body {
  background: #fff;
  font-family: "roboto";
  width: 100vw;
  z-index: 1;
  position: relative;
  overflow-x: hidden;
}
body::-webkit-scrollbar {
  width: 0;
}

section {
  padding: 50px 0 0;
  position: relative;
  z-index: 1;
}

.title {
  font-weight: 600;
  font-size: 36px;
  line-height: 1;
  color: #1b1a1a;
  margin: 0 0 20px;
}
.title span {
  color: #6c63ff;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

.section-anim {
  opacity: 0;
  transform: translateY(40px);
  transition: all 1s;
}
.section-anim.section-anim__active {
  opacity: 1;
  transform: translateY(0);
}

.btn__blc {
  border-radius: 10px;
  background: #1b1a1a;
  padding: 11px 22px;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  color: #fff;
  display: block;
  border: none;
  position: relative;
}
.btn__wht {
  border-radius: 10px;
  background: #fff;
  padding: 11px 22px;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  color: #1b1a1a;
  display: block;
  border: none;
  position: relative;
}
.btn__anim {
  display: block;
  position: relative;
}
.btn__anim b {
  display: block;
  position: relative;
  overflow: hidden;
}
.btn__anim i {
  display: block;
  width: 100%;
  position: relative;
}
.btn__anim i + i {
  position: absolute;
  left: 0;
  top: 100%;
}
.btn__anim span {
  display: inline-block;
  position: relative;
  transition: transform 0.6s cubic-bezier(0.76, 0, 0.24, 1);
}
.btn__anim:hover span {
  transform: translateY(-100%);
}

.input {
  position: relative;
}
.input p {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.1;
  color: #fff;
  position: absolute;
  left: 0;
  top: 0;
  transition: all 0.3s;
  transform-origin: 0% 50%;
  pointer-events: none;
}
.input input {
  background: transparent;
  padding: 0 0 5px;
  border: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.7);
  box-shadow: none;
  color: #fff;
  width: 100%;
  box-sizing: border-box;
  transition: all 0.3s;
}
.input input:focus, .input input:active, .input input:not(:placeholder-shown) {
  border-bottom: 1px solid #fff;
}
.input input:focus + p, .input input:active + p, .input input:not(:placeholder-shown) + p {
  transform: translateY(-100%) scale(0.8);
}
.input__check {
  position: relative;
  display: grid;
  grid-template-columns: 18px 1fr;
  gap: 10px;
}
.input__check p {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.1;
  color: #fff;
}
.input__check p a {
  color: #6c63ff;
  position: relative;
  display: inline-block;
}
.input__check p a::before {
  content: "";
  display: block;
  position: absolute;
  top: 100%;
  width: 0;
  right: 0;
  height: 1px;
  background: #6c63ff;
  transition: all 0.3s;
}
.input__check p a::after {
  content: "";
  display: block;
  position: absolute;
  top: 100%;
  height: 1px;
  background: #6c63ff;
  transition: all 0.3s;
  opacity: 1;
  left: 0;
  width: 100%;
  transition-delay: 0.1s;
}
.input__check p a:hover::after {
  right: 0;
  width: 0;
  left: auto;
  transition-delay: 0s;
}
.input__check p a:hover::before {
  left: 0;
  right: auto;
  width: 100%;
  transition-delay: 0.1s;
}
.input__check span {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 18px;
  height: 18px;
  border-radius: 3px;
  border: 1px solid #fff;
  transition: all 0.3s;
}
.input__check span svg {
  opacity: 0;
  transition: all 0.3s;
}
.input__check input {
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 100%;
  opacity: 0;
  cursor: pointer;
  z-index: 2;
}
.input__check input:checked + span {
  background: #fff;
}
.input__check input:checked + span svg {
  opacity: 1;
}

.nav {
  padding: 20px 0;
}
.nav__logo {
  max-width: 140px;
}
.nav__logo img {
  width: 100%;
}
.nav .container {
  display: flex;
  justify-content: start;
  align-items: center;
}
.nav ul {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 32px;
  margin: 0 32px 0 auto;
}
.nav ul a {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.6;
  color: #1b1a1a;
  transition: all 0.3s;
  position: relative;
}
.nav ul a::before {
  content: "";
  display: block;
  position: absolute;
  top: 100%;
  width: 0;
  right: 0;
  height: 1px;
  background: #1b1a1a;
  transition: all 0.3s;
}
.nav ul a:hover::before {
  left: 0;
  right: auto;
  width: 100%;
}

.body__bg {
  width: 421px;
  height: 421px;
  opacity: 0.7;
  transition: all 0.3s;
  position: absolute;
  z-index: -1;
}

.header {
  padding: 120px 0;
  background: #ece7e4;
  position: relative;
  z-index: 1;
  overflow: hidden;
}
.header h1 {
  font-weight: 600;
  font-size: 64px;
  line-height: 1;
  color: #1b1a1a;
}
.header h1 span {
  color: #6c63ff;
}
.header__subtitle {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.1;
  color: #403e3b;
  margin: 30px 0 0;
  max-width: 500px;
}
.header .container {
  display: grid;
  grid-template-columns: 1fr;
  gap: 30px;
}
.header__form {
  padding: 40px 20px;
  border-radius: 30px;
  background: #403e3b;
  max-width: 400px;
}
.header__form_text {
  font-weight: 400;
  font-size: 20px;
  line-height: 1.1;
  color: #fff;
}
.header__form .btn__wht {
  margin: 20px 0 0;
  width: 100%;
}
.header .input {
  margin: 30px 0 0;
}
.header .input__check {
  margin: 15px 0 0;
}
.header .body__bg {
  top: 20px;
  right: -50px;
}

.service__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 20px;
}
.service__item {
  border-radius: 30px;
  padding: 20px;
  background: #ece7e4;
  transition: all 0.3s;
  cursor: default;
}
.service__item svg {
  width: 24px;
  height: 24px;
}
.service__item svg path {
  fill: #6c63ff;
  transition: all 0.3s;
}
.service__item p {
  font-weight: 800;
  font-size: 20px;
  line-height: 1.5;
  color: #1b1a1a;
  transition: all 0.3s;
}
.service__item p + p {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.2;
  margin: 10px 0 0;
}
.service__item:hover {
  background: #403e3b;
}
.service__item:hover p {
  color: #fff;
}
.service__item:hover svg path {
  fill: #fff;
}
.service .body__bg {
  top: 20px;
  left: 150px;
  width: 320px;
  height: 320px;
}

.footer {
  margin: 50px 0 0;
  padding: 50px 0 25px;
  background: #403e3b;
}
.footer__block {
  display: grid;
  grid-template-columns: 220px 1fr 3fr auto;
  gap: 35px;
  align-items: start;
}
.footer__descr {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.6;
  color: #fff;
  transition: all 0.3s;
  position: relative;
}
.footer__logo img {
  width: 100%;
  filter: invert(1);
}
.footer__list {
  display: grid;
  grid-template-columns: 1fr;
  gap: 12px;
  margin: 0 auto;
}
.footer__list a {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.6;
  color: #fff;
  transition: all 0.3s;
  position: relative;
}
.footer__list a::before {
  content: "";
  display: block;
  position: absolute;
  top: 100%;
  width: 0;
  right: 0;
  height: 1px;
  background: #fff;
  transition: all 0.3s;
}
.footer__list a:hover::before {
  left: 0;
  right: auto;
  width: 100%;
}
.footer__soc {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.footer__soc a {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  width: 35px;
  height: 35px;
  background: #fff;
}
.footer__soc a svg {
  width: 24px;
  height: 24px;
}
.footer__soc a svg path {
  fill: #1b1a1a;
  transition: all 0.3s;
}
.footer__soc a:hover path {
  fill: #6c63ff;
}
.footer__row {
  display: flex;
  justify-content: space-between;
  align-items: start;
  margin: 50px 0 0;
}
.footer__row p,
.footer__row a {
  color: #fff;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.2;
}
.footer__row a {
  transition: all 0.3s;
}
.footer__row a:hover {
  color: #6c63ff;
}

.faq__block {
  display: grid;
  grid-template-columns: 400px 1fr;
  gap: 30px;
  position: relative;
  align-items: start;
}
.faq__title {
  width: 400px;
}
.faq__title.fix {
  position: fixed;
  top: 20px;
}
.faq__title.bottom {
  position: absolute;
  left: 0;
  bottom: 0;
}
.faq__title_text {
  font-size: 16px;
  color: #1b1a1a;
  margin: 0 0 10px;
  gw: 500px;
}
.faq__title_text span {
  color: #6c63ff;
}
.faq__wrapper {
  grid-column: 2/3;
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
}
.faq__wrapper .btn__blc,
.faq__wrapper .faq__title_text {
  display: none;
}
.faq__item {
  border-bottom: 1px solid #1b1a1a;
  padding: 0 0 20px;
}
.faq__item:last-child {
  padding: 0;
  border-bottom: none;
}
.faq__info {
  max-height: 0;
  overflow: hidden;
  transition: all 0.3s;
}
.faq__info p {
  font-size: 16px;
  color: #403e3b;
}
.faq__btn {
  display: grid;
  grid-template-columns: 1fr 20px;
  gap: 20px;
  align-items: center;
  cursor: pointer;
}
.faq__btn p {
  font-size: 20px;
  font-weight: 600;
  transition: all 0.3s;
  color: #1b1a1a;
}
.faq__btn span {
  position: relative;
  display: block;
  width: 20px;
  height: 20px;
}
.faq__btn span::before, .faq__btn span::after {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  background: #1b1a1a;
  transition: all 0.3s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.faq__btn span::after {
  transform: translate(-50%, -50%) rotate(90deg);
}
.faq__btn:hover p, .faq__btn.active p {
  color: #6c63ff;
}
.faq__btn:hover span::after, .faq__btn:hover span::before, .faq__btn.active span::after, .faq__btn.active span::before {
  background: #6c63ff;
}
.faq__btn.active span::after {
  transform: translate(-50%, -50%);
}
.faq__btn.active + .faq__info {
  max-height: 200px;
  padding: 20px 0 0;
}
.faq .body__bg {
  top: 20px;
  left: 30px;
  width: 380px;
  height: 380px;
}